// /home/coffee/my-keymap-viewer/src/hooks/useNumberPractice.ts
import { useState, useCallback, useMemo, useEffect, useRef } from 'react';
import {
    PracticeHookProps,
    PracticeHookResult,
    PracticeInputInfo,
    PracticeInputResult as HandleInputResult,
    PracticeStatus,
    PracticeHighlightResult,
} from './usePracticeCommons';

const HEADER_NUMBERS = "1234567890";

// 全角数字を半角数字に変換するヘルパー関数
const toHalfWidth = (str: string): string => {
    return str.replace(/[０-９]/g, (s) => String.fromCharCode(s.charCodeAt(0) - 0xFEE0));
};

const useNumberPractice = ({
    gIdx, // この練習では gIdx はヘッダーの文字インデックスとして使う
    // dIdx, // この練習では dIdx は直接使わない
    isActive,
    onAdvance,
    layers,
}: PracticeHookProps): PracticeHookResult => {
    // 'targetDigit' (1-9の数字キー待ち) または 'targetZero' (0キー待ち)
    const [internalStage, setInternalStage] = useState<'targetDigit' | 'targetZero'>('targetDigit');
    const [status, setStatus] = useState<PracticeStatus>('waiting');
    const [errorHighlightLayer, setErrorHighlightLayer] = useState<number | null>(null); // エラーハイライト用

    const prevIsActiveRef = useRef(isActive);
    const prevGIdxRef = useRef(gIdx);

    // console.log(`[NumberPractice RENDER] Hook initialized/rendered. isActive: ${isActive}, gIdx (prop): ${gIdx}, internalStage: ${internalStage}, status: ${status}`);

    const reset = useCallback(() => {
        console.log('[NumberPractice reset] Resetting states...');
        setInternalStage('targetDigit');
        setStatus('waiting');
        setErrorHighlightLayer(null); // エラー状態もリセット
        // gIdx は usePracticeManagement でリセットされる想定 (props経由で更新される)
    }, [setInternalStage, setStatus]);

    useEffect(() => {
        console.log(`[NumberPractice useEffect START] isActive: ${isActive}, gIdx (prop): ${gIdx}, prevIsActive: ${prevIsActiveRef.current}, prevGIdx: ${prevGIdxRef.current}, current internalStage: ${internalStage}, current status: ${status}`);
        if (!isActive && prevIsActiveRef.current) {
            console.log('[NumberPractice useEffect] Practice became inactive. Calling reset().');
            reset();
        } else if (isActive) {
            // gIdx が変わった時、またはアクティブになった直後は targetDigit に
            if (gIdx !== prevGIdxRef.current || (isActive && !prevIsActiveRef.current)) {
                console.log(`[NumberPractice useEffect] gIdx changed (from ${prevGIdxRef.current} to ${gIdx}) or practice just activated. Setting internalStage to 'targetDigit' and status to 'waiting'.`);
                setInternalStage('targetDigit');
                setErrorHighlightLayer(null); // ステージ変更時にエラーもクリア
                setStatus('waiting');
            }
        }
        prevIsActiveRef.current = isActive;
        prevGIdxRef.current = gIdx;
        console.log(`[NumberPractice useEffect END] Updated refs: prevIsActive=${prevIsActiveRef.current}, prevGIdx=${prevGIdxRef.current}. Current internalStage: ${internalStage}, status: ${status}`);
    }, [gIdx, isActive, reset, setInternalStage, setStatus]);

    const handleInput = useCallback((input: PracticeInputInfo): HandleInputResult => {
        console.log(`[NumberPractice handleInput START] Input pressCode: 0x${input.pressCode.toString(16)}, type: ${input.type}. Current state: isActive=${isActive}, internalStage=${internalStage}, gIdx=${gIdx}, status=${status}`);

        if (!isActive || !layers) {
            // console.log(`[NumberPractice handleInput] PRE-CONDITION FAILED. isActive: ${isActive}, layers: ${!!layers}`);
            return { isExpected: false, shouldGoToNext: false, errorLayer: null };
        }

        if (input.type !== 'release') {
            // console.log(`[NumberPractice handleInput] Not a 'release' event. Type: ${input.type}. Skipping.`);
            return { isExpected: false, shouldGoToNext: false };
        }

        const currentTargetDigitChar = HEADER_NUMBERS[gIdx]; // ヘッダーでハイライトされている数字
        let expectedKeyName: string | null = null;

        if (internalStage === 'targetDigit') {
            expectedKeyName = currentTargetDigitChar;
        } else if (internalStage === 'targetZero') {
            expectedKeyName = "0";
        }

        // 数字キーはレイヤー0にあると仮定
        let originalPressedKeyNameOnLayer0: string | null = null;
        let normalizedPressedKeyName: string | null = null;

        if (layers[0] && input.pressCode >= 1 && (input.pressCode - 1) < layers[0].length) {
            // input.pressCode が既に0始まりのインデックスであると仮定して -1 を削除
            originalPressedKeyNameOnLayer0 = layers[0][input.pressCode]?.trim() ?? null;
            if (originalPressedKeyNameOnLayer0) {
                normalizedPressedKeyName = toHalfWidth(originalPressedKeyNameOnLayer0);
            }
            console.log(`[NumberPractice handleInput] Key Lookup: input.pressCode=${input.pressCode}, layers[0][${input.pressCode}]="${layers[0][input.pressCode]}". Original: "${originalPressedKeyNameOnLayer0}", Normalized: "${normalizedPressedKeyName}", Expected: "${expectedKeyName}"`);
        }

        console.log(`[NumberPractice handleInput BEFORE COMPARISON] expectedKeyName: "${expectedKeyName}", normalizedPressedKeyName: "${normalizedPressedKeyName}"`);

        if (expectedKeyName && normalizedPressedKeyName && normalizedPressedKeyName === expectedKeyName) {
            setStatus('correct');
            if (internalStage === 'targetDigit') {
                console.log(`[NumberPractice handleInput] Digit "${expectedKeyName}" MATCH. Setting internalStage to 'targetZero'.`);
                setInternalStage('targetZero');
                setErrorHighlightLayer(null); // 正解なのでエラークリア                
                // 数字キー(1-9)の入力成功は中間ステップなので shouldGoToNext は false
                return { isExpected: true, shouldGoToNext: false, errorLayer: null };
            } else if (internalStage === 'targetZero') {
                console.log(`[NumberPractice handleInput] Zero MATCH. Setting internalStage to 'targetDigit', status to 'waiting', and calling onAdvance.`);
                setInternalStage('targetDigit');
                // onAdvance?.(); // App.tsx (via usePracticeManagement) が shouldGoToNext: true に基づいて gIdx を進めます。
                setErrorHighlightLayer(null); // 正解なのでエラークリア
                setStatus('waiting'); // 次の入力を待つ状態に
                // 0キーの入力成功はアイテム完了なので shouldGoToNext は true
                return { isExpected: true, shouldGoToNext: true, errorLayer: null };
            }
        }
        console.log(`[NumberPractice handleInput AFTER COMPARISON / NO MATCH] Expected: "${expectedKeyName}", Pressed (normalized): "${normalizedPressedKeyName}". Setting status to 'incorrect'.`);
        setStatus('incorrect');
        setErrorHighlightLayer(0); // 数字キーはレイヤー0でエラー
        // 間違えた場合、ステージは変更しない（同じキーを再度待つ）
        return { isExpected: false, shouldGoToNext: false, errorLayer: 0, errorPressCode: input.pressCode };
    }, [isActive, internalStage, gIdx, onAdvance, layers, setInternalStage, setStatus]);

    const getHighlightClassName = useCallback((keyName: string, layoutIndex: number): PracticeHighlightResult => {
        // エラーハイライトがアクティブな場合、通常の期待キーハイライトは表示しない
        if (errorHighlightLayer === layoutIndex) {
            return { className: null, overrideKey: null };
        }
        console.log(`[NumberPractice getHighlightClassName START] keyName: "${keyName}", layoutIndex: ${layoutIndex}, isActive: ${isActive}, internalStage: ${internalStage}, gIdx: ${gIdx}`);
        if (!isActive || layoutIndex !== 0) { // レイヤー0のみ考慮
            // console.log(`[NumberPractice getHighlightClassName] Not active or not layer 0. Returning no highlight.`);
            return { className: null, overrideKey: null };
        }

        const currentTargetCharFromHeader = HEADER_NUMBERS[gIdx]; // ヘッダーで示される現在のターゲット数字 (例: "1", "2")
        let expectedKeyToHighlight: string | null = null;

        const normalizedKeyName = toHalfWidth(keyName.trim()); // キー名を半角に正規化
        console.log(`[NumberPractice getHighlightClassName] Normalized keyName: "${normalizedKeyName}" (original: "${keyName.trim()}"), currentTargetCharFromHeader: "${currentTargetCharFromHeader}"`);

        if (internalStage === 'targetDigit') {
            // ヘッダーで示されている数字キー (1-9) の入力を待つ
            expectedKeyToHighlight = currentTargetCharFromHeader;
        } else if (internalStage === 'targetZero') {
            // "0" キーの入力を待つ
            expectedKeyToHighlight = "0";
        }

        // 期待するキーと、実際にキーボードに表示されている文字（正規化後）が一致するか確認
        if (expectedKeyToHighlight && normalizedKeyName === expectedKeyToHighlight) {
            console.log(`[NumberPractice getHighlightClassName] MATCH: Normalized key "${normalizedKeyName}" === Expected key "${expectedKeyToHighlight}". Returning highlight.`);
            return { className: 'bg-blue-100', overrideKey: null };
        }
        
        console.log(`[NumberPractice getHighlightClassName] NO MATCH: Normalized key "${normalizedKeyName}", Expected key to highlight: "${expectedKeyToHighlight}". Returning no highlight.`);
        return { className: null, overrideKey: null };
    }, [isActive, internalStage, gIdx]);

    const headingChars = useMemo(() => HEADER_NUMBERS.split(''), []);

    const isInvalidInputTarget = useCallback((targetPressCode: number, currentLayoutIndex: number, currentKeyIndex: number, pressedLayerIndex?: number): boolean => {
        if (!isActive || status !== 'incorrect') return false;

        // pressedLayerIndex が渡されていれば、それと currentLayoutIndex が一致するか確認
        // 数字練習ではエラーは常にレイヤー0で発生する想定
        if (pressedLayerIndex !== undefined) {
            return currentLayoutIndex === pressedLayerIndex && currentLayoutIndex === 0 && currentKeyIndex === targetPressCode;
        }
        // pressedLayerIndex が渡されていない場合 (フォールバック)
        return currentLayoutIndex === 0 && currentKeyIndex === targetPressCode;
    }, [isActive, status]);

    const clearErrorState = useCallback(() => {
        setErrorHighlightLayer(null);
        setStatus('waiting'); // エラークリア時は waiting に戻す
    }, [setErrorHighlightLayer, setStatus]);

    return {
        status, // status を返すようにする
        handleInput,
        getHighlightClassName,
        currentTarget: HEADER_NUMBERS[gIdx] || undefined, // 現在のターゲット数字 (ヘッダーハイライト用)
        reset,
        targetLayerIndex: null, // 数字練習では特定の単一レイヤーをターゲットにしない想定
        headingChars,
        targetLayerIndices: [0], // 数字練習では基本レイアウト (レイヤー0) を表示
        isInvalidInputTarget,
        errorHighlightLayer, // エラーハイライトレイヤーを返す
        clearErrorState,     // エラークリア関数を返す
    };
};

export default useNumberPractice;
