import { useMemo, useState, useEffect, useCallback, useRef } from 'react';
import { functionKeyMaps } from '../data/keymapData'; // functionKeyMaps は不要かもしれないが、念のため残す
import {
    PracticeHookProps,
    PracticeInputInfo,
    PracticeInputResult,
    PracticeHookResult,
    CharInfoAlphaSymbol1, // 新しい型をインポート
    allAlphaSymbol1CharInfos, // 新しいデータ配列をインポート
    PracticeHighlightResult,
    PracticeStatus,
} from './usePracticeCommons';

// Define stages for the practice mode
type AlphaSymbolPractice1Stage = 'initialSymbolKey' | 'targetSymbolChar';

const useAlphaSymbolPractice1 = ({
    isActive,
    layers,
}: PracticeHookProps): PracticeHookResult => {
    const [status, setStatus] = useState<PracticeStatus>('idle');
    const [stage, setStage] = useState<AlphaSymbolPractice1Stage>('initialSymbolKey');
    const prevIsActiveRef = useRef(isActive);
    const [sequentialTargetIndex, setSequentialTargetIndex] = useState<number>(0);

    // The actual symbol to be typed (e.g., '@', '-', '_', '/', '°')
    const actualSymbolTarget = useMemo(() => {
        if (sequentialTargetIndex < 0 || sequentialTargetIndex >= allAlphaSymbol1CharInfos.length) {
            return null;
        }
        const char = allAlphaSymbol1CharInfos[sequentialTargetIndex]?.char ?? null;
        console.log(`[AlphaSymbolPractice1 actualSymbolTarget] sequentialTargetIndex: ${sequentialTargetIndex}, char: ${char}`);
        return char;
    }, [sequentialTargetIndex]);

    // This is what the user is currently expected to press on the keyboard
    const currentKeyboardExpectedInput = useMemo(() => {
        if (stage === 'initialSymbolKey') return '記号';
        return actualSymbolTarget;
    }, [stage, actualSymbolTarget]);
    
    // What the user is currently expected to type (either "記号" or the actualSymbolTarget)
    const currentExpectedInputChar = useMemo(() => {
        if (stage === 'initialSymbolKey') return '記号';
        return actualSymbolTarget;
    }, [stage, actualSymbolTarget]);

    // ヘッダー文字 (固定)
    const headingChars = useMemo(() => {
        if (!isActive) return [];
        return ['@', '-', '_', '/', '°'];
    }, [isActive]);

    // Advance to the next symbol in the sequence
    const advanceToNextSymbol = useCallback(() => {
        setSequentialTargetIndex(prevIndex => {
            const nextIndex = (prevIndex + 1) % allAlphaSymbol1CharInfos.length;
            console.log(`[AlphaSymbolPractice1 advanceToNextSymbol] prevIndex: ${prevIndex}, nextIndex: ${nextIndex}`);
            return nextIndex;
        });
        setStage('initialSymbolKey'); // Always expect "記号" key first for the new symbol
        setStatus('waiting');
    }, [setSequentialTargetIndex, setStage, setStatus]); // allAlphaSymbol1CharInfos.length is stable

    // リセット関数
    const reset = useCallback(() => {
        setStage('initialSymbolKey');
        setStatus('idle');
        setSequentialTargetIndex(0); // Reset to the first symbol
    }, [setStage, setStatus, setSequentialTargetIndex]);

    // 初期化とアクティブ状態の監視
    useEffect(() => {
        if (!isActive && prevIsActiveRef.current) {
            reset();
        }
        if (isActive) {
            setStatus('waiting');
            // If becoming active, and we want to ensure it starts from index 0
            // or if the target isn't set (which shouldn't happen with sequentialTargetIndex)
            if (!prevIsActiveRef.current) { // Just became active
                setSequentialTargetIndex(0); // Ensure it starts from the first symbol
                setStage('initialSymbolKey');
            }
        }
        prevIsActiveRef.current = isActive; // Update ref at the end
    }, [isActive, reset, setStatus, setSequentialTargetIndex, setStage]); // Removed randomTarget and selectNextRandomTarget

    // 入力処理
    const handleInput = useCallback((inputInfo: PracticeInputInfo): PracticeInputResult => {
        if (!isActive || !actualSymbolTarget) {
            return { isExpected: false, shouldGoToNext: false, errorLayer: null };
        }
        if (inputInfo.type !== 'release') {
            return { isExpected: false, shouldGoToNext: false, errorLayer: null };
        }

        const pressCode = inputInfo.pressCode; // 0-based
        const layer9 = layers[9] ?? [];
        const pressedKeyOnLayer9 = layer9[pressCode]?.trim();

        let isExpected = false;
        let nextStatus: PracticeStatus = status;
        let errorLayer: number | null = null;
        let shouldGoToNextSymbol = false;

        if (stage === 'initialSymbolKey') {
            if (pressedKeyOnLayer9 === '記号') {
                isExpected = true;
                setStage('targetSymbolChar'); // Transition to expect the symbol character
                nextStatus = 'waiting';
            } else {
                isExpected = false;
                nextStatus = 'incorrect';
                errorLayer = 9;
            }
        } else if (stage === 'targetSymbolChar') {
            if (pressedKeyOnLayer9 && pressedKeyOnLayer9 === actualSymbolTarget) {
                isExpected = true;
                nextStatus = 'correct';
                console.log(`[AlphaSymbolPractice1 handleInput] Correct target symbol. Calling advanceToNextSymbol. Current actualSymbolTarget: ${actualSymbolTarget}`);
                advanceToNextSymbol(); // Advance to the next symbol in the sequence
            } else {
                isExpected = false;
                nextStatus = 'incorrect';
                errorLayer = 9;
                setStage('initialSymbolKey'); // 不正解時は「記号」キー入力に戻す
            }
        }

        setStatus(nextStatus);

        return { isExpected, shouldGoToNext: false, errorLayer }; // shouldGoToNext from hook is for gyou/dan, not this logic
    }, [
        isActive,
        layers,
        actualSymbolTarget,
        stage,
        setStatus,
        advanceToNextSymbol,
        status,
    ]);

    // ハイライト処理
    const getHighlightClassName = useCallback((keyName: string, layoutIndex: number): PracticeHighlightResult => {
        const noHighlight: PracticeHighlightResult = { className: null, overrideKey: null };
        if (!isActive) { // 練習がアクティブでない場合はハイライトしない
            return noHighlight;
        }

        // レイヤー9のみを対象
        if (layoutIndex === 9) {
            const trimmedKeyName = keyName.trim();
            if (stage === 'initialSymbolKey' && trimmedKeyName === '記号') {
                return { className: 'bg-blue-100', overrideKey: null }; // 青色でハイライト
            }
            if (stage === 'targetSymbolChar' && actualSymbolTarget && trimmedKeyName === actualSymbolTarget) {
                return { className: 'bg-blue-100', overrideKey: null }; // 青色でハイライト
            }
        }
        return noHighlight;
    }, [
        isActive,
        stage,
        actualSymbolTarget,
    ]);

    // 不正入力ターゲット判定 (この練習モードではエラーレイヤー上の全てのキーを不正入力としない)
    const isInvalidInputTarget = useCallback((pressCode: number, layoutIndex: number, keyIndex: number): boolean => {
        if (!isActive || status !== 'incorrect') return false;
        // エラーが発生したレイヤー(9)で、実際に押されたキー(pressCode)と、
        // 現在レンダリング中のキー(keyIndex)が一致する場合にtrue
        return layoutIndex === 9 && keyIndex === pressCode;
    }, [isActive, status]);

    const clearErrorState = useCallback(() => {
        setStatus('waiting'); // エラークリア時は waiting に戻す
    }, [setStatus]);

    return {
        headingChars,
        handleInput,
        getHighlightClassName,
        reset,
        status,
        targetChar: actualSymbolTarget ?? undefined, // Header should highlight the actual symbol
        errorHighlightLayer: status === 'incorrect' ? 9 : null,
        isInvalidInputTarget,
        clearErrorState,
        targetLayerIndex: 9, // 常にレイヤー9をターゲットとする
        targetLayerIndices: [9], // 表示するレイヤーもレイヤー9のみ
    };
};

export default useAlphaSymbolPractice1;
