// /home/coffee/my-keymap-viewer/src/hooks/useAlphabetPractice.ts
import { useState, useCallback, useMemo, useEffect, useRef } from 'react';
import {
    PracticeHookProps,
    PracticeHookResult,
    PracticeInputInfo,
    PracticeInputResult as HandleInputResult,
    PracticeStatus,
    PracticeHighlightResult,
} from './usePracticeCommons';
import { alphabetPracticeGroups } from '../data/keymapData'; // グループ情報をインポート

// Define the type for internalStage explicitly
type AlphabetPracticeStage = 'waitingForGroupKey' | 'waitingForCharKey' | 'finished';

const useAlphabetPractice = ({
    gIdx,
    dIdx,
    isActive,
    onAdvance,
    layers,
}: PracticeHookProps): PracticeHookResult => {
    const [internalStage, setInternalStage] = useState<AlphabetPracticeStage>('waitingForGroupKey');
    const [currentTargetGroup, setCurrentTargetGroup] = useState<string>('');
    const [status, setStatus] = useState<PracticeStatus>('waiting');
    const [errorLayer, setErrorLayer] = useState<number | null>(null); // エラーレイヤーを管理するstate

    const prevIsActiveRef = useRef(isActive);
    const prevGIdxRef = useRef(gIdx);
    const prevDIdxRef = useRef(dIdx);

    // console.log(`[AlphabetPractice] Hook initialized/rendered. isActive: ${isActive}, gIdx: ${gIdx}, dIdx: ${dIdx}, internalStage: ${internalStage}`);

    const reset = useCallback(() => {
        // console.log('[AlphabetPractice reset]');
        setInternalStage('waitingForGroupKey');
        setCurrentTargetGroup('');
        setStatus('waiting');
        setErrorLayer(null); // エラーレイヤーをリセット
        prevGIdxRef.current = -1;
        prevDIdxRef.current = -1;
    }, [setInternalStage, setCurrentTargetGroup, setStatus, setErrorLayer]);

    useEffect(() => {
        console.log(`[AlphabetPractice useEffect] START. isActive: ${isActive}, gIdx: ${gIdx}, dIdx: ${dIdx}, prevGIdx: ${prevGIdxRef.current}, prevDIdx: ${prevDIdxRef.current}, internalStage: ${internalStage}, currentTargetGroup: "${currentTargetGroup}"`);
        const justActivated = isActive && !prevIsActiveRef.current;

        if (!isActive && prevIsActiveRef.current) {
            console.log('[AlphabetPractice useEffect] Practice became inactive. Calling reset().');
            reset();
        } else if (isActive) {
            if (gIdx < alphabetPracticeGroups.length) {
                const group = alphabetPracticeGroups[gIdx];
                if (group !== currentTargetGroup) {
                    setCurrentTargetGroup(group);
                }

                // If gIdx or dIdx changed, or if the practice was just activated,
                // reset to waitingForGroupKey for the current/new target.
                if (justActivated || gIdx !== prevGIdxRef.current || dIdx !== prevDIdxRef.current) {
                    console.log(`[AlphabetPractice useEffect] Resetting to 'waitingForGroupKey'. Reason: justActivated=${justActivated}, gIdx changed=${gIdx !== prevGIdxRef.current}, dIdx changed=${dIdx !== prevDIdxRef.current}`);
                    setInternalStage('waitingForGroupKey');
                    setStatus('waiting');
                    setErrorLayer(null); // ステージリセット時にエラーレイヤーもクリア
                }
            } else {
                // All groups completed
                if (internalStage !== 'finished') { // Avoid redundant calls
                    console.log('[AlphabetPractice useEffect] All groups completed. Setting stage to finished.');
                    setInternalStage('finished');
                    setStatus('finished');
                    setErrorLayer(null);
                    setCurrentTargetGroup(''); // Clear target group
                }
            }
        }
        prevIsActiveRef.current = isActive;
        prevGIdxRef.current = gIdx;
        prevDIdxRef.current = dIdx;
        console.log(`[AlphabetPractice useEffect] END. Updated refs. currentTargetGroup: "${currentTargetGroup}", internalStage: ${internalStage}, status: ${status}, prevGIdx: ${prevGIdxRef.current}, prevDIdx: ${prevDIdxRef.current}`);
    }, [isActive, gIdx, dIdx, alphabetPracticeGroups, reset, internalStage, currentTargetGroup, setInternalStage, setStatus, setErrorLayer]);


    const handleInput = useCallback((input: PracticeInputInfo): HandleInputResult => {
        console.log(`[AlphabetPractice handleInput] CALLED. isActive: ${isActive}, internalStage: ${internalStage}, currentTargetGroup: "${currentTargetGroup}", gIdx: ${gIdx}, dIdx: ${dIdx}`);
        console.log(`[AlphabetPractice handleInput] Input details: type=${input.type}, pressCode=0x${input.pressCode.toString(16)}`);

        if (!isActive || internalStage === 'finished' || !currentTargetGroup || !layers) {
            console.log(`[AlphabetPractice handleInput] PRE-CONDITION FAILED. isActive: ${isActive}, internalStage: ${internalStage}, currentTargetGroup: "${currentTargetGroup}", layers: ${!!layers}`);
            return { isExpected: false, shouldGoToNext: false, errorLayer: null };
        }

        if (input.type !== 'release') {
            console.log(`[AlphabetPractice handleInput] Not a 'release' event. Type: ${input.type}. Skipping.`);
            return { isExpected: false, shouldGoToNext: false, errorLayer: null };
        }

        let pressedKeyNameOnLayer4: string | null = null;
        if (layers[4] && input.pressCode >= 0 && input.pressCode < layers[4].length) {
            pressedKeyNameOnLayer4 = layers[4][input.pressCode]?.trim();
            console.log(`[AlphabetPractice handleInput] Key Lookup Layer 4: input.pressCode=${input.pressCode}, layers[4][${input.pressCode}]="${layers[4][input.pressCode]}" -> pressedKeyNameOnLayer4="${pressedKeyNameOnLayer4}"`);
        }

        let pressedKeyNameOnLayer5: string | null = null;
        if (layers[5] && input.pressCode >= 0 && input.pressCode < layers[5].length) {
            pressedKeyNameOnLayer5 = layers[5][input.pressCode]?.trim();
            console.log(`[AlphabetPractice handleInput] Key Lookup Layer 5: input.pressCode=${input.pressCode}, layers[5][${input.pressCode}]="${layers[5][input.pressCode]}" -> pressedKeyNameOnLayer5="${pressedKeyNameOnLayer5}"`);
        }

        const charInputKeyNames = ["①", "②", "③", "④", "⑤", "⑥", "⑦", "⑧", "⑨", "⑩", "⑪", "⑫", "⑬", "⑭", "⑮", "⑯", "⑰", "⑱", "⑲", "⑳"];
        const expectedCharKeyName = (dIdx < charInputKeyNames.length && dIdx < currentTargetGroup.length) ? charInputKeyNames[dIdx] : null;

        let isCorrectInput = false;
        let nextInternalStage: AlphabetPracticeStage = internalStage;
        let currentErrorLayer: number | null = null; // この関数スコープでのエラーレイヤー

        if (internalStage === 'waitingForGroupKey') {
            console.log(`[AlphabetPractice handleInput] Stage: waitingForGroupKey. Pressed Layer 4: "${pressedKeyNameOnLayer4}", Expected Group: "${currentTargetGroup}"`);
            if (pressedKeyNameOnLayer4 && pressedKeyNameOnLayer4.toLowerCase() === currentTargetGroup.toLowerCase()) {
                console.log('[AlphabetPractice handleInput] Group key MATCH. Transitioning to waitingForCharKey.');
                isCorrectInput = true;
                nextInternalStage = 'waitingForCharKey';
                setStatus('waiting'); // Prepare for char key input
                currentErrorLayer = null;
            } else {
                 console.log('[AlphabetPractice handleInput] Group key NO MATCH.');
                 isCorrectInput = false;
                 // nextInternalStage remains 'waitingForGroupKey'
                 currentErrorLayer = 4; // グループキーはレイヤー4
            }
        } else if (internalStage === 'waitingForCharKey') {
            console.log(`[AlphabetPractice handleInput] Stage: waitingForCharKey. Pressed Layer 5: "${pressedKeyNameOnLayer5}", Expected Char Key: "${expectedCharKeyName}" (for char: ${currentTargetGroup[dIdx]})`);
            if (expectedCharKeyName && pressedKeyNameOnLayer5 && pressedKeyNameOnLayer5 === expectedCharKeyName) {
                console.log('[AlphabetPractice handleInput] Char key MATCH. Calling onAdvance. Transitioning to waitingForGroupKey.');
                isCorrectInput = true;
                // onAdvance?.(); // App.tsx (via usePracticeManagement) が shouldGoToNext: true に基づいて dIdx/gIdx を進めます。
                nextInternalStage = 'waitingForGroupKey'; // Next input will be group key for the *next* character/group
                // status will be set to 'correct' below, and useEffect will set it to 'waiting' for the new target
                currentErrorLayer = null;
                // Signal App.tsx to advance the practice item (dIdx/gIdx)
                return { isExpected: isCorrectInput, shouldGoToNext: true, errorLayer: currentErrorLayer };
            } else {
                console.log('[AlphabetPractice handleInput] Char key NO MATCH.');
                isCorrectInput = false;
                // nextInternalStage remains 'waitingForCharKey'
                currentErrorLayer = 5; // 文字キーはレイヤー5
            }
        }

        setStatus(isCorrectInput ? 'correct' : 'incorrect');
        setErrorLayer(currentErrorLayer); // フックの state を更新

        if (nextInternalStage !== internalStage) {
            console.log(`[AlphabetPractice handleInput] Stage changing from ${internalStage} to ${nextInternalStage}`);
            setInternalStage(nextInternalStage);
        }

        return { isExpected: isCorrectInput, shouldGoToNext: false, errorLayer: currentErrorLayer }; // グループキー入力成功時は shouldGoToNext は false

    }, [isActive, internalStage, currentTargetGroup, gIdx, dIdx, onAdvance, layers, setInternalStage, setStatus, setErrorLayer]);


    const getHighlightClassName = useCallback((keyName: string, layoutIndex: number): PracticeHighlightResult => {
        if (!isActive || internalStage === 'finished' || !currentTargetGroup || status === 'incorrect') {
            return { className: null, overrideKey: null };
        }

        const currentGroupKeyNameFromState = currentTargetGroup;
        const charInputKeyNames = ["①", "②", "③", "④", "⑤", "⑥", "⑦", "⑧", "⑨", "⑩", "⑪", "⑫", "⑬", "⑭", "⑮", "⑯", "⑰", "⑱", "⑲", "⑳"];
        const expectedCharKeyName = (dIdx < charInputKeyNames.length && dIdx < currentGroupKeyNameFromState.length) ? charInputKeyNames[dIdx] : null;

        // console.log(`[AlphabetPractice getHighlightClassName] Current Stage: ${internalStage}, Target Group: "${currentGroupKeyNameFromState}", Target Char Key: "${expectedCharKeyName}" (for char: ${currentTargetGroup[dIdx]})`);

        if (internalStage === 'waitingForGroupKey') {
            if (layoutIndex === 4 && keyName.trim().toLowerCase() === currentGroupKeyNameFromState.toLowerCase()) {
                // console.log(`[AlphabetPractice getHighlightClassName] Stage: ${internalStage}. Highlighting group key "${keyName.trim()}" on layer 4.`);
                return { className: 'bg-blue-100 dark:bg-blue-800', overrideKey: null };
            }
        } else if (internalStage === 'waitingForCharKey') {
            if (layoutIndex === 5 && expectedCharKeyName && keyName.trim() === expectedCharKeyName) {
                // console.log(`[AlphabetPractice getHighlightClassName] Stage: ${internalStage}. Highlighting char key "${keyName.trim()}" on layer 5 for char "${currentGroupKeyNameFromState[dIdx]}".`);
                return { className: 'bg-blue-100 dark:bg-blue-800', overrideKey: null };
            }
        }
        return { className: null, overrideKey: null };
    }, [isActive, internalStage, currentTargetGroup, dIdx, status]); // alphabetPracticeGroups は不要


    const headingChars = useMemo(() => {
        if (gIdx < alphabetPracticeGroups.length) {
            return alphabetPracticeGroups[gIdx].split('');
        }
        return [];
    }, [gIdx]); // alphabetPracticeGroups は定数なので依存配列から削除可能

    const isInvalidInputTarget = useCallback((
        invalidlyPressedKeyCode: number, // 0-based code of the key that was actually pressed and found invalid
        keyBeingRenderedLayoutIndex: number,
        keyBeingRenderedIndex: number // 0-based index of the key being rendered
    ): boolean => {
        if (!isActive || status !== 'incorrect' || errorLayer === null) {
            return false;
        }
        // Highlight the key if it's on the layer where the error occurred
        // AND it's the key that was actually pressed.
        return keyBeingRenderedLayoutIndex === errorLayer && keyBeingRenderedIndex === invalidlyPressedKeyCode;
    }, [isActive, status, errorLayer]);

    const clearErrorState = useCallback(() => {
        console.log('[AlphabetPractice clearErrorState] CALLED. Clearing error layer and setting status to waiting.');
        setErrorLayer(null);
        setStatus('waiting'); // エラークリア時に status を waiting に戻す
    }, [setErrorLayer, setStatus]);

    return {
        status,
        handleInput,
        getHighlightClassName,
        targetChar: currentTargetGroup && dIdx < currentTargetGroup.length
            ? currentTargetGroup[dIdx]
            : undefined,
        reset,
        targetLayerIndex: null, // この練習では特定の単一レイヤーをターゲットにしない
        headingChars,
        targetLayerIndices: [4, 5], // グループキーはレイヤー4、文字キーはレイヤー5
        isInvalidInputTarget,
        errorLayer, // errorLayer を返す
        clearErrorState, // clearErrorState を返す
    };
};

export default useAlphabetPractice;
