// /home/coffee/my-keymap-viewer/src/hooks/useGairaigoPractice.ts
import { useState, useEffect, useCallback, useMemo, useRef } from 'react';
import {
    PracticeHookProps, PracticeHookResult, PracticeInputInfo, PracticeInputResult,
    getHidKeyCodes,
    PracticeHighlightResult,
    CharInfoGairaigo,
    allGairaigoCharInfos,
    PracticeStage, // <<< PracticeStage をインポート
} from './usePracticeCommons';
import { gairaigoPracticeData, GairaigoPracticeTarget } from '../data/keymapData';

// import { stat } from 'fs'; // fsモジュールはブラウザ環境では使えないためコメントアウトまたは削除

const useGairaigoPractice = ({
    gIdx, dIdx, isActive, side, layers, kb, isRandomMode // isRandomMode を受け取る
}: PracticeHookProps): PracticeHookResult => {
    const [stage, setStage] = useState<PracticeStage>('key1'); // Use common PracticeStage
    const [pressedKeys, setPressedKeys] = useState<Map<number, number>>(new Map());
    const [randomTarget, setRandomTarget] = useState<CharInfoGairaigo | null>(null);
    const prevGIdxRef = useRef(gIdx);
    const prevDIdxRef = useRef(dIdx);
    const isInitialMount = useRef(true);
    const prevTargetCharRef = useRef<string | null>(null); // Ref to store previous target char
    const currentTargetRef = useRef<CharInfoGairaigo | null>(null); // Ref to store current target object
    const prevIsRandomModeRef = useRef(isRandomMode);
    const prevIndicesRef = useRef({ gIdx, dIdx }); // Store previous gIdx and dIdx
    const prevIsActiveRef = useRef(isActive); // isActive の変更を追跡 (命名変更)
    const [errorHighlightLayer, setErrorHighlightLayer] = useState<number | null>(null); // エラーハイライト用
    const [status, setStatus] = useState<'waiting' | 'correct' | 'incorrect'>('waiting'); // 練習の状態

    const selectNextRandomTarget = useCallback(() => {
        if (allGairaigoCharInfos.length > 0) {
            const randomIndex = Math.floor(Math.random() * allGairaigoCharInfos.length);
            const nextTarget = allGairaigoCharInfos[randomIndex];
            console.log(`[Gairaigo selectNextRandomTarget] New random target: ${nextTarget.char}`);
            setRandomTarget(nextTarget);
            setStage('key1'); // 常に最初のステージから
            setPressedKeys(new Map()); // キー押下状態もリセット
            setErrorHighlightLayer(null);
        } else {
            setRandomTarget(null);
        }
    }, [setRandomTarget, setStage, setPressedKeys]);
    
    const reset = useCallback(() => {
        setStage('key1');
        setPressedKeys(new Map());
        setRandomTarget(null); // ランダムターゲットもリセット
        prevTargetCharRef.current = null; // Reset previous target char
        prevGIdxRef.current = -1;
        prevDIdxRef.current = -1;
        isInitialMount.current = true;
        prevIsRandomModeRef.current = false;
        currentTargetRef.current = null; // Reset currentTargetRef
        setErrorHighlightLayer(null);
        setStatus('waiting');
        // prevIsActiveRef.current は useEffect の最後で更新されるのでここでは不要
    }, [setStage, setPressedKeys, setRandomTarget]);

    // Make it a standalone function accepting props
    const calculateCurrentTarget = useCallback((
        currentGIdx: number,
        currentDIdx: number,
        currentIsRandomMode: boolean,
        currentIsActive: boolean,
        currentRandomTarget: CharInfoGairaigo | null
    ): CharInfoGairaigo | null => { // Changed return type
        console.log(`[Gairaigo calculateCurrentTarget] Args: gIdx=${currentGIdx}, dIdx=${currentDIdx}, isRandomMode=${currentIsRandomMode}, isActive=${currentIsActive}, randomTarget=${currentRandomTarget?.char}`);
        if (currentIsRandomMode) {
            return currentRandomTarget; // This is already CharInfoGairaigo | null
        } else {
            // 通常モードのロジック
            if (!currentIsActive || currentGIdx < 0 || currentGIdx >= gairaigoPracticeData.length) return null;
            const currentGroup = gairaigoPracticeData[currentGIdx]; // Use passed gIdx
            // App.tsx から渡される dIdx は、このグループの targets 配列のインデックスとして直接使用する
            if (!currentGroup || !currentGroup.targets || currentDIdx < 0 || currentDIdx >= currentGroup.targets.length) { // currentDIdx を使用
                console.warn(`[Gairaigo calculateCurrentTarget] No target found for gIdx=${currentGIdx}, dIdx=${currentDIdx}`);
                return null;
            }

            const gptTarget = currentGroup.targets[currentDIdx]; // currentDIdx を使用
            // console.log(`[Gairaigo calculateCurrentTarget] Found target for gIdx=${currentGIdx}, dIdx=${currentDIdx}: ${gptTarget?.char}`);

            if (!gptTarget) return null;
            return { ...gptTarget, type: 'gairaigo' }; // Convert to CharInfoGairaigo
        }
    }, []); // No dependencies, it's a pure function based on arguments

    useEffect(() => {
        console.log(`[Gairaigo useEffect START] isActive=${isActive}, gIdx=${gIdx}, dIdx=${dIdx}, isRandomMode=${isRandomMode}, prevIsActive=${prevIsActiveRef.current}, prevGIdx=${prevGIdxRef.current}, prevDIdx=${prevDIdxRef.current}, prevIsRandomMode=${prevIsRandomModeRef.current}`);
        if (!isActive && prevIsActiveRef.current) {
            reset();
        } else if (isActive) {
            const gIdxChanged = prevGIdxRef.current !== gIdx;
            const dIdxChanged = prevDIdxRef.current !== dIdx;
            const randomModeChanged = prevIsRandomModeRef.current !== isRandomMode;
            const justActivated = !prevIsActiveRef.current && isActive;

            console.log(`[Gairaigo useEffect] Conditions: gIdxChanged=${gIdxChanged}, dIdxChanged=${dIdxChanged}, randomModeChanged=${randomModeChanged}, justActivated=${justActivated}`);
            if (randomModeChanged) {
                reset();
                if (isRandomMode) {
                    selectNextRandomTarget();
                }
            } else if (justActivated) { // 練習モードがアクティブになった直後 (isActive が false -> true に変わった)
                if (isRandomMode) {
                    selectNextRandomTarget(); // ランダムモードなら新しいターゲットを選択
                } else {
                    const newTarget = calculateCurrentTarget(gIdx, dIdx, false, true, null);
                    currentTargetRef.current = newTarget;
                    console.log(`[Gairaigo useEffect justActivated] Normal mode. gIdx=${gIdx}, dIdx=${dIdx}. New target: ${newTarget?.char}`);
                    setStage('key1');
                    setPressedKeys(new Map());
                    setStatus('waiting');
                    setErrorHighlightLayer(null);
                }
                isInitialMount.current = false; // 初期マウントフラグを解除
            } else if (!isRandomMode && (gIdxChanged || dIdxChanged)) { // 通常モードで、かつ gIdx または dIdx が変更された場合
                let newTarget = calculateCurrentTarget(gIdx, dIdx, false, true, null);
                // gIdx が範囲外になった場合、gIdx を 0 にリセットして再試行 (usePracticeManagement でループするようになったので不要かもしれない)
                // if (newTarget === null && gIdx >= gairaigoPracticeData.length) {
                //     console.log(`[Gairaigo useEffect gIdx/dIdx changed] gIdx ${gIdx} is out of bounds. Resetting gIdx to 0.`);
                //     newTarget = calculateCurrentTarget(0, 0, false, true, null); // gIdx=0, dIdx=0 で再計算
                //     console.log(`[Gairaigo useEffect gIdx/dIdx changed] Recalculated target with gIdx=0, dIdx=0. New target: ${newTarget?.char}`);
                // }
                currentTargetRef.current = newTarget; // newTarget が null の場合もそのまま設定
                console.log(`[Gairaigo useEffect gIdx/dIdx changed] Normal mode. Original gIdx=${gIdx}, dIdx=${dIdx}. Final target: ${currentTargetRef.current?.char}`);
                setStage('key1');
                setPressedKeys(new Map()); // 押下キーもリセット
                setStatus('waiting');
                setErrorHighlightLayer(null);
                // isInitialMount は変更しない (既に false のはず)
            } else if (isRandomMode && !randomTarget && !isInitialMount.current) { // ランダムモードでターゲットがない場合 (初期マウント後)
                console.log(`[Gairaigo useEffect] Random mode, no target, not initial mount. Selecting new random target.`);
                selectNextRandomTarget();
            }
        }

        prevIsActiveRef.current = isActive;
        prevGIdxRef.current = gIdx;
        prevDIdxRef.current = dIdx;
        prevIndicesRef.current = { gIdx, dIdx }; // Update prevIndicesRef here
        prevIsRandomModeRef.current = isRandomMode;
    }, [isActive, gIdx, dIdx, isRandomMode, reset, selectNextRandomTarget, randomTarget, calculateCurrentTarget]);
    

    const currentTarget = currentTargetRef.current; // Use the ref for currentTarget

    const expectedKey1Codes = useMemo(() => currentTarget ? getHidKeyCodes(currentTarget.keys[0], layers, kb, side) : [], [currentTarget, layers, kb, side]);
    const expectedKey2Codes = useMemo(() => currentTarget ? getHidKeyCodes(currentTarget.actualSecondKey, layers, kb, side) : [], [currentTarget, layers, kb, side]);
    const expectedKey3Codes = useMemo(() => {
        if (!currentTarget) return [];
        const keyToUse = currentTarget.actualThirdKey ?? currentTarget.keys[2];
        return getHidKeyCodes(keyToUse, layers, kb, side);
    }, [currentTarget, layers, kb, side]);
    const expectedKey4Codes = useMemo(() => {
        if (currentTarget && currentTarget.keys.length > 3 && currentTarget.keys[3]) {
            return getHidKeyCodes(currentTarget.keys[3], layers, kb, side);
        }
        return [];
    }, [currentTarget, layers, kb, side]);


    const handleInput = useCallback((info: PracticeInputInfo): PracticeInputResult => {
        if (!isActive || !currentTarget) {
            setErrorHighlightLayer(null);
            return { isExpected: false, shouldGoToNext: undefined, errorLayer: null };
        }

        let isExpected = false;
        let shouldGoToNext_final: boolean | undefined = undefined;
        const { type, pressCode } = info;
        let errorPressCodeToReturn: number | undefined = undefined;
        let errorExpectedLayerToReturn: number | undefined = undefined;
        let errorLayerForHook: number | null = null;

        if (type === 'press') {
            setPressedKeys(prev => new Map(prev).set(pressCode, Date.now()));
            return { isExpected: false, shouldGoToNext: undefined, errorLayer: currentTargetRef.current ? errorHighlightLayer : null }; // ターゲットがある場合のみエラーレイヤーを返す
        }

        // --- Release Event ---
        let nextInternalStage: PracticeStage = stage;

        if (stage === 'key1') {
            console.log(`[Gairaigo handleInput key1] Target: ${currentTarget.char}, Expected key1: ${currentTarget.keys[0]}, Expected codes: [${expectedKey1Codes.join(', ')}], pressCode: 0x${pressCode.toString(16)}`);
            if (expectedKey1Codes.includes(pressCode + 1)) { // pressCode を 1-based にして比較
                console.log(`[Gairaigo handleInput key1] Key1 MATCH! (pressCode + 1 = ${pressCode + 1})`);
                nextInternalStage = 'key2';
                isExpected = true;
                errorLayerForHook = null;
            } else {
                console.log(`[Gairaigo handleInput key1] Key1 NO MATCH.`);
                nextInternalStage = 'key1';
                errorPressCodeToReturn = pressCode;
                errorExpectedLayerToReturn = 2; // Key1 is expected on layer 2
                errorLayerForHook = 2;
            }
        } else if (stage === 'key2') {
            if (expectedKey2Codes.includes(pressCode + 1)) { // pressCode を 1-based にして比較
                isExpected = true;
                errorLayerForHook = null;
                if (currentTarget.keys.length > 2) {
                    nextInternalStage = 'key3';
                } else {
                    nextInternalStage = 'key1';
                    if (isRandomMode) {
                        selectNextRandomTarget();
                        shouldGoToNext_final = false;
                    } else {
                        // 2打鍵で完了する文字の場合、ここで練習を進める
                        shouldGoToNext_final = true;
                    }
                }
            } else {
                nextInternalStage = 'key1';
                errorPressCodeToReturn = pressCode;
                errorExpectedLayerToReturn = 2; // Key2 is expected on layer 2
                errorLayerForHook = 2;
            }
        } else if (stage === 'key3') {
            if (expectedKey3Codes.includes(pressCode + 1)) { // pressCode を 1-based にして比較
                isExpected = true;
                errorLayerForHook = null;
                if (currentTarget.keys.length > 3) {
                    nextInternalStage = 'key4';
                } else {
                    nextInternalStage = 'key1';
                    if (isRandomMode) {
                        selectNextRandomTarget();
                        shouldGoToNext_final = false;
                    } else {
                        // 3打鍵で完了する文字の場合、ここで練習を進める
                        shouldGoToNext_final = true;
                    }
                }
            } else {
                nextInternalStage = 'key1';
                errorPressCodeToReturn = pressCode;
                const thirdKey = currentTarget.actualThirdKey ?? currentTarget.keys[2];
                errorExpectedLayerToReturn = thirdKey === '濁音' ? 2 : (thirdKey.endsWith('段') ? 3 : 2);
                errorLayerForHook = errorExpectedLayerToReturn;
            }
        } else if (stage === 'key4') {
            if (expectedKey4Codes.includes(pressCode + 1)) { // pressCode を 1-based にして比較
                isExpected = true;
                errorLayerForHook = null;
                nextInternalStage = 'key1';
                if (isRandomMode) {
                    selectNextRandomTarget();
                    shouldGoToNext_final = false;
                } else {
                    const currentGroupData = gairaigoPracticeData[gIdx];
                    const isLastPracticeItemInGroup = currentGroupData ? dIdx === currentGroupData.targets.length - 1 : false;
                    shouldGoToNext_final = isLastPracticeItemInGroup;
                }
            } else {
                nextInternalStage = 'key1';
                errorPressCodeToReturn = pressCode;
                errorExpectedLayerToReturn = 3; // Key4 is expected on layer 3
                errorLayerForHook = 3;
            }
        }

        setErrorHighlightLayer(errorLayerForHook);
        setStatus(isExpected ? 'correct' : 'incorrect');

        if (nextInternalStage !== stage) {
            setStage(nextInternalStage);
        } else if (!isExpected && stage !== 'key1') {
            setStage('key1');
        }

        return {
            isExpected,
            shouldGoToNext: shouldGoToNext_final, // undefined の場合 App.tsx が gIdx, dIdx を進めない
            errorPressCode: errorPressCodeToReturn,
            errorExpectedLayer: errorExpectedLayerToReturn,
            errorLayer: errorLayerForHook, // フック内部のエラーレイヤーも返す
        };
    }, [
        isActive, stage, currentTarget,
        expectedKey1Codes, expectedKey2Codes, expectedKey3Codes, expectedKey4Codes,
        setStage, isRandomMode, selectNextRandomTarget, gIdx, dIdx, setErrorHighlightLayer, setStatus, errorHighlightLayer
    ]);


    const headingChars = useMemo(() => {
        if (!isActive) return [];

        if (isRandomMode) {
            return currentTarget ? [currentTarget.char] : [];
        } else {
            const currentGroup = gairaigoPracticeData[gIdx] ?? null;
            return currentGroup?.headerChars ?? [];
        }
    }, [isActive, isRandomMode, currentTarget, gIdx]);

    const getHighlightClassName = useCallback((key: string, layoutIndex: number): PracticeHighlightResult => {
        const noHighlight: PracticeHighlightResult = { className: null, overrideKey: null };
        if (!isActive || !currentTarget) {
            return noHighlight;
        }
        if (errorHighlightLayer === layoutIndex) {
            return noHighlight; // エラーレイヤーのキーはハイライトしない (isInvalidInputTarget で処理)
        }

        const currentStageForHighlight = stage;

        let expectedKeyName = '';
        let displayKeyName = '';
        let targetLayoutIndex: number | null = null;

        if (currentStageForHighlight === 'key1') {
            expectedKeyName = currentTarget.keys[0];
            displayKeyName = currentTarget.keys[0] ?? '';
            targetLayoutIndex = 2;
            // console.log(`[Gairaigo getHighlightClassName Key1] currentTarget: ${currentTarget.char}, expectedKeyName: ${expectedKeyName}, displayKeyName: ${displayKeyName}, targetLayoutIndex: ${targetLayoutIndex}, received key: ${key}, received layoutIndex: ${layoutIndex}`);
        } else if (currentStageForHighlight === 'key2') {
            expectedKeyName = currentTarget.actualSecondKey;
            displayKeyName = currentTarget.keys[1];
            targetLayoutIndex = 2;
            // console.log(`[Gairaigo getHighlightClassName Key2] currentTarget: ${currentTarget.char}, expectedKeyName: ${expectedKeyName}, displayKeyName: ${displayKeyName}, targetLayoutIndex: ${targetLayoutIndex}, received key: ${key}, received layoutIndex: ${layoutIndex}`);
        } else if (currentStageForHighlight === 'key3') {
            expectedKeyName = currentTarget.actualThirdKey ?? currentTarget.keys[2];
            displayKeyName = currentTarget.keys[2];
            if (currentTarget.actualThirdKey === '濁音' || (!currentTarget.actualThirdKey && currentTarget.keys[2] === '濁音')) {
                targetLayoutIndex = 2;
            } else if ((currentTarget.actualThirdKey && currentTarget.actualThirdKey.endsWith('段')) || (!currentTarget.actualThirdKey && currentTarget.keys[2].endsWith('段'))) {
                targetLayoutIndex = 3;
            } else {
                 targetLayoutIndex = 2;
            }
            // console.log(`[Gairaigo getHighlightClassName Key3] currentTarget: ${currentTarget.char}, expectedKeyName: ${expectedKeyName}, displayKeyName: ${displayKeyName}, targetLayoutIndex: ${targetLayoutIndex}, received key: ${key}, received layoutIndex: ${layoutIndex}`);
        } else if (currentStageForHighlight === 'key4') {
            const fourthKey = currentTarget.keys.length > 3 ? currentTarget.keys[3] : undefined;
            expectedKeyName = typeof fourthKey === 'string' ? fourthKey : '';
            displayKeyName = expectedKeyName;
            targetLayoutIndex = 3;
            // console.log(`[Gairaigo getHighlightClassName Key4] currentTarget: ${currentTarget.char}, expectedKeyName: ${expectedKeyName}, displayKeyName: ${displayKeyName}, targetLayoutIndex: ${targetLayoutIndex}, received key: ${key}, received layoutIndex: ${layoutIndex}`);
        }

        if (expectedKeyName === null || expectedKeyName === undefined) {
             return noHighlight;
        }

        if (layoutIndex === targetLayoutIndex && key === expectedKeyName) {
            return {
                className: 'bg-blue-100',
                overrideKey: displayKeyName
            };
        }

        return noHighlight;
    }, [stage, currentTarget, isActive, errorHighlightLayer]);

    const isInvalidInputTarget = useCallback((pressCode: number, layoutIndex: number, idx: number): boolean => {
        if (!isActive || !currentTarget) return false;
        if (errorHighlightLayer !== layoutIndex) return false; // エラーレイヤーでない場合は不正入力ターゲットではない

        // エラーレイヤー上で、実際に押されたキー (pressCode) と、現在レンダリング中のキー (idx) が一致する場合に true
        return idx === pressCode;
    }, [isActive, currentTarget, errorHighlightLayer]);

    const clearErrorState = useCallback(() => {
        setErrorHighlightLayer(null);
        setStatus('waiting'); // エラークリア時は waiting に戻す
    }, [setErrorHighlightLayer, setStatus]);


    return {
        handleInput,
        headingChars,
        getHighlightClassName,
        reset,
        isInvalidInputTarget,
        currentTarget: currentTarget ?? undefined,
        stage,
        errorHighlightLayer,
        clearErrorState,
        status, // status を返す
        targetLayerIndex: useMemo(() => { // targetLayerIndex を動的に計算
            if (stage === 'key1' || stage === 'key2' || (stage === 'key3' && (currentTarget?.actualThirdKey === '濁音' || (!currentTarget?.actualThirdKey && currentTarget?.keys[2] === '濁音')))) {
                return 2;
            } else if (stage === 'key4' || (stage === 'key3' && ((currentTarget?.actualThirdKey && currentTarget.actualThirdKey.endsWith('段')) || (!currentTarget?.actualThirdKey && currentTarget?.keys[2].endsWith('段'))))) {
                return 3;
            }
            return undefined; // 不明な場合は undefined
        }, [stage, currentTarget]),
        targetLayerIndices: useMemo(() => { // targetLayerIndices を動的に計算
            const defaultLayers = [2, 3];
            if (errorHighlightLayer !== null) {
                const layersToShow = new Set(defaultLayers);
                layersToShow.add(errorHighlightLayer);
                return Array.from(layersToShow);
            }
            return defaultLayers;
        }, [errorHighlightLayer]),
    };
};

export default useGairaigoPractice;
