// /home/coffee/my-keymap-viewer/src/hooks/useYouonKakuchoPractice.ts
import { useState, useEffect, useCallback, useMemo, useRef } from 'react';
import {
    PracticeHookProps, PracticeHookResult, PracticeInputInfo, PracticeInputResult,
    getHidKeyCodes, hid2Youon,
    PracticeHighlightResult,
    PracticeStage,
    hid2GyouHRight_Kana,
    hid2GyouHLeft_Kana,
    hid2DanHRight_Kana,
    hid2DanHLeft_Kana,
    hid2GyouVRight_Kana,
    hid2GyouVLeft_Kana,
    hid2DanVRight_Kana,
    hid2DanVLeft_Kana,
} from './usePracticeCommons';
import {
    youonGyouList,
    youonKakuchoChars,
    youonKakuchoPracticeData,
    YouonKakuchoInputDef,
    functionKeyMaps,
} from '../data/keymapData';

export interface YouonKakuchoHeadingCharInfo {
    char: string;
    isPracticeTarget: boolean;
}

const useYouonKakuchoPractice = ({
    gIdx, dIdx, isActive, side, layers, kb, isRandomMode, onAdvance
}: PracticeHookProps): PracticeHookResult => {
    const [stage, setStage] = useState<PracticeStage>('line');
    const [randomTarget, setRandomTarget] = useState<{ g: number; d: number } | null>(null);
    const prevGIdxRef = useRef(gIdx);
    const prevDIdxRef = useRef(dIdx);
    const isInitialMount = useRef(true);
    const prevIsActiveRef = useRef(isActive);
    const prevIsRandomModeRef = useRef(isRandomMode);
    const [isSpecialRowHighlightActive, setIsSpecialRowHighlightActive] = useState(false);
    const specialHighlightTimerRef = useRef<NodeJS.Timeout | null>(null);
    const [currentErrorHighlightLayer, setCurrentErrorHighlightLayer] = useState<number | null>(null);

    const hid2GyouMap = useMemo(() => {
        if (kb === 'tw-20v') {
            return side === 'left' ? hid2GyouVLeft_Kana : hid2GyouVRight_Kana;
        } else { // TW-20H
            return side === 'left' ? hid2GyouHLeft_Kana : hid2GyouHRight_Kana;
        }
    }, [kb, side]);

    const hid2DanMap = useMemo(() => {
        if (kb === 'tw-20v') {
            return side === 'left' ? hid2DanVLeft_Kana : hid2DanVRight_Kana;
        } else { // TW-20H
            return side === 'left' ? hid2DanHLeft_Kana : hid2DanHRight_Kana;
        }
    }, [kb, side]);

    const currentGIdx = isRandomMode ? (randomTarget?.g ?? 0) : gIdx;
    const currentDIdx = isRandomMode ? (randomTarget?.d ?? 0) : dIdx;

    const currentGyouKey = useMemo(() => {
        const practiceGroup = youonKakuchoPracticeData[currentGIdx];
        if (practiceGroup) {
            const validDef = practiceGroup.inputs.find(def => def !== null);
            if (validDef) {
                return validDef.gyouKey;
            }
        }
        return youonGyouList[currentGIdx] ?? youonGyouList[0];
    }, [currentGIdx]);

    const headingChars = useMemo((): YouonKakuchoHeadingCharInfo[] => {
        if (!isActive) return [];
        // console.log(`[YouonKakucho headingChars CALC] isActive: ${isActive}, isRandomMode: ${isRandomMode}, gIdx: ${gIdx}, dIdx (prop): ${dIdx}, randomTarget: ${JSON.stringify(randomTarget)}`);

        const currentGIdxForHeading = isRandomMode ? (randomTarget?.g ?? 0) : gIdx;
        // ランダムモードのハイライト決定に使う dIdx (通常モードのヘッダー内容には影響しない)
        const currentDIdxForRandomHighlight = isRandomMode ? (randomTarget?.d ?? 0) : dIdx;

        if (isRandomMode) {
            const randomPracticeGroup = youonKakuchoPracticeData[currentGIdxForHeading];
            const randomGyouKey = randomPracticeGroup?.inputs.find(def => def !== null)?.gyouKey ?? (youonGyouList[currentGIdxForHeading] || youonGyouList[0]);
            const randomDisplayString = youonKakuchoChars[randomGyouKey]?.[0] ?? "";
            const randomDisplayableChars = randomDisplayString.split('');
            return randomDisplayableChars.map((char: string, index: number) => ({
                char: char,
                isPracticeTarget: Math.floor(index / 2) === currentDIdxForRandomHighlight,
            }));
        }

        // 通常モード
        const currentGroupData = youonKakuchoPracticeData[currentGIdxForHeading];
        if (!currentGroupData) {
            return [];
        }

        const gyouKeyForHeading = currentGroupData.inputs.find(def => def !== null)?.gyouKey ?? (youonGyouList[currentGIdxForHeading] || youonGyouList[0]);
        
        const charsArrayForHeading = youonKakuchoChars[gyouKeyForHeading]; 
        if (!charsArrayForHeading || charsArrayForHeading.length === 0) {
            return [];
        }
        
        // 配列の最初の要素が "きゃきぃきゅきぇきょ" のような文字列全体であることを期待
        const fullDisplayString = charsArrayForHeading[0]; 
        const displayableChars = fullDisplayString.split(''); 
        const conceptualInputs = currentGroupData.inputs;

        const result = displayableChars.map((char: string, index: number) => {
            const conceptualInputIndex = Math.floor(index / 2);
            const isTarget = conceptualInputs?.[conceptualInputIndex] !== null && conceptualInputs?.[conceptualInputIndex] !== undefined;
            return {
                char: char,
                isPracticeTarget: isTarget,
            };
        });
        return result;
    }, [isActive, isRandomMode, gIdx, dIdx, randomTarget]); // dIdx はランダムモード時のハイライト決定に影響するため依存配列に含める

    const currentInputDef = useMemo((): YouonKakuchoInputDef | null => {
        if (isRandomMode) {
            if (randomTarget) {
                return youonKakuchoPracticeData[randomTarget.g]?.inputs[randomTarget.d] ?? null;
            }
            return null;
        }
        return youonKakuchoPracticeData[currentGIdx]?.inputs[currentDIdx] ?? null;
    }, [isRandomMode, randomTarget, currentGIdx, currentDIdx]);
    
    const targetChar = useMemo(() => {
        return currentInputDef?.char ?? '';
    }, [currentInputDef]);

    const targetDan = useMemo(() => {
        return currentInputDef?.dan ?? '';
    }, [currentInputDef]);

    const generateRandomTarget = useCallback(() => {
        const randomG = Math.floor(Math.random() * youonKakuchoPracticeData.length);
        const inputsInGroup = youonKakuchoPracticeData[randomG]?.inputs;
        if (!inputsInGroup || inputsInGroup.length === 0) {
            setRandomTarget(null);
            return;
        }
        const validPracticeIndices: number[] = [];
        inputsInGroup.forEach((inputDef, index) => {
            if (inputDef !== null) {
                validPracticeIndices.push(index);
            }
        });

        if (validPracticeIndices.length === 0) {
            setRandomTarget(null);
            return;
        }
        const randomTargetD = validPracticeIndices[Math.floor(Math.random() * validPracticeIndices.length)];
        setRandomTarget({ g: randomG, d: randomTargetD });
        setStage('line');
        setCurrentErrorHighlightLayer(null);
        setIsSpecialRowHighlightActive(false);
    }, []);

    const selectNextRandomTarget = useCallback(() => {
        generateRandomTarget();
    }, [generateRandomTarget]);

    const currentFunctionKeyMap = useMemo(() => functionKeyMaps[kb]?.[side] ?? {}, [kb, side]);

    const reset = useCallback(() => {
        setStage('line');
        setRandomTarget(null);
        prevGIdxRef.current = -1;
        prevDIdxRef.current = -1;
        isInitialMount.current = true;
        prevIsRandomModeRef.current = false;
        setCurrentErrorHighlightLayer(null);
        setIsSpecialRowHighlightActive(false);
        if (specialHighlightTimerRef.current) {
            clearTimeout(specialHighlightTimerRef.current);
            specialHighlightTimerRef.current = null;
        }
    }, []);

    useEffect(() => {
        if (!isActive && prevIsActiveRef.current) {
            reset();
        }
        if (isActive) {
            if (isActive && !prevIsActiveRef.current) {
                isInitialMount.current = true;
            }
            const indicesChanged = gIdx !== prevGIdxRef.current || dIdx !== prevDIdxRef.current;
            const randomModeChangedToTrue = isRandomMode && !prevIsRandomModeRef.current;
            const randomModeChangedToFalse = !isRandomMode && prevIsRandomModeRef.current;

            if (randomModeChangedToFalse || (!isRandomMode && (isInitialMount.current || indicesChanged))) {
                setStage('line');
                setRandomTarget(null);
                setCurrentErrorHighlightLayer(null);
                setIsSpecialRowHighlightActive(false);
                prevGIdxRef.current = gIdx;
                prevDIdxRef.current = dIdx;
                isInitialMount.current = false;
            } else if (isRandomMode && (randomModeChangedToTrue || isInitialMount.current || !randomTarget)) {
                 selectNextRandomTarget();
                 setCurrentErrorHighlightLayer(null);
                 isInitialMount.current = false;
                 prevGIdxRef.current = gIdx;
                 prevDIdxRef.current = dIdx;
            }
        }
        prevIsActiveRef.current = isActive;
        prevIsRandomModeRef.current = isRandomMode;
        return () => {
            if (specialHighlightTimerRef.current) {
                clearTimeout(specialHighlightTimerRef.current);
            }
        };
    }, [isActive, isRandomMode, gIdx, dIdx, randomTarget, reset, selectNextRandomTarget]);

    const handleInput = useCallback((info: PracticeInputInfo): PracticeInputResult => {
        if (info.type !== 'release' || !isActive || !currentInputDef) {
            return { isExpected: false, shouldGoToNext: undefined, errorLayer: currentErrorHighlightLayer };
        }

        const pressCode = info.pressCode;
        let isExpected = false;
        let shouldGoToNext_final: boolean | undefined = undefined;
        let nextStageInternal: PracticeStage = stage;
        let errorLayer: number | null = null;

        const youonKeyCodeEntry = Object.entries(currentFunctionKeyMap).find(([_, name]) => name === '拗音');
        const expectedYouonKeyCode = youonKeyCodeEntry ? parseInt(youonKeyCodeEntry[0]) + 1 : -1;

        if (expectedYouonKeyCode === -1) {
            console.error("useYouonKakuchoPractice: Could not find key code for '拗音'.");
            return { isExpected: false, shouldGoToNext: undefined, errorLayer: null };
        }

        switch (stage) {
            case 'line':
                const expectedGyouKeyCodes = Object.entries(hid2GyouMap)
                    .filter(([_, name]) => name === currentInputDef.gyouKey)
                    .map(([codeStr, _]) => parseInt(codeStr));
                if (expectedGyouKeyCodes.includes(pressCode + 1)) {
                    isExpected = true;
                    nextStageInternal = 'youon';
                    errorLayer = null;
                } else {
                    errorLayer = 2;
                }
                break;
            case 'youon':
                 if ((pressCode + 1) === expectedYouonKeyCode) {
                    isExpected = true;
                    nextStageInternal = 'dan';
                    errorLayer = null;
                } else {
                    nextStageInternal = 'line';
                    errorLayer = 2;
                }
                break;
            case 'dan':
                const expectedDanKeyCodes = Object.entries(hid2DanMap)
                    .filter(([_, name]) => name === currentInputDef.dan)
                    .map(([codeStr, _]) => parseInt(codeStr));
                nextStageInternal = 'line';
                if (expectedDanKeyCodes.includes(pressCode + 1)) {
                    isExpected = true;
                    errorLayer = null;
                    if (isRandomMode) {
                        selectNextRandomTarget();
                        shouldGoToNext_final = false;
                    } else {
                        if (currentDIdx === 3) {
                            shouldGoToNext_final = true;
                        } else if (currentDIdx === 1) {
                            onAdvance?.();
                            shouldGoToNext_final = undefined;
                        } else {
                            shouldGoToNext_final = true;
                        }
                        if (currentDIdx === 1 || currentDIdx === 3) {
                            setIsSpecialRowHighlightActive(true);
                            if (specialHighlightTimerRef.current) clearTimeout(specialHighlightTimerRef.current);
                            specialHighlightTimerRef.current = setTimeout(() => {
                                setIsSpecialRowHighlightActive(false);
                                specialHighlightTimerRef.current = null;
                            }, 500);
                        }
                    }
                } else {
                    errorLayer = 3;
                }
                break;
        }
        setCurrentErrorHighlightLayer(errorLayer);
        if (nextStageInternal !== stage) {
            setStage(nextStageInternal);
        } else if (!isExpected && stage !== 'line') {
             setStage('line');
        }
        return { isExpected, shouldGoToNext: shouldGoToNext_final, errorLayer };
    }, [
        isActive, stage, currentInputDef, hid2GyouMap, hid2DanMap, currentFunctionKeyMap,
        isRandomMode, setStage, selectNextRandomTarget, currentDIdx, onAdvance, setIsSpecialRowHighlightActive
    ]);

    const getHighlightClassName = useCallback((keyName: string, layoutIndex: number): PracticeHighlightResult => {
        const noHighlight: PracticeHighlightResult = { className: null, overrideKey: null };
        if (!isActive || (currentErrorHighlightLayer === layoutIndex)) {
            return noHighlight;
        }
        if (!isRandomMode && (currentDIdx !== 1 && currentDIdx !== 3)) {
            return noHighlight;
        }
        if (!currentInputDef) {
            return noHighlight;
        }

        const currentStageForHighlight = stage;
        let expectedKeyName: string | null = null;
        let targetLayoutIndex: number | null = null;
        const youonKeyEntry = Object.entries(currentFunctionKeyMap).find(([_, name]) => name === '拗音');
        const youonDisplayName = youonKeyEntry ? currentFunctionKeyMap[parseInt(youonKeyEntry[0])] : '拗音';

        switch (currentStageForHighlight) {
            case 'line':
                expectedKeyName = currentInputDef.gyouKey;
                targetLayoutIndex = 2;
                break;
            case 'youon':
                expectedKeyName = youonDisplayName;
                targetLayoutIndex = 2;
                break;
            case 'dan':
                expectedKeyName = currentInputDef.dan;
                targetLayoutIndex = 3;
                break;
        }
        if (expectedKeyName !== null && layoutIndex === targetLayoutIndex && keyName.trim() === expectedKeyName.trim()) {
            return { className: 'bg-blue-100', overrideKey: null };
        }
        return noHighlight;
    }, [
        isActive, stage, currentInputDef, currentFunctionKeyMap, isRandomMode, currentErrorHighlightLayer, currentDIdx
    ]);

    const isInvalidInputTarget = useCallback((pressCode: number, layoutIndex: number, keyIndex: number): boolean => {
        return isActive && currentErrorHighlightLayer === layoutIndex && keyIndex === pressCode;
    }, [isActive, currentErrorHighlightLayer]);

    const clearErrorState = useCallback(() => {
        setCurrentErrorHighlightLayer(null);
    }, []);

    const currentTarget = useMemo(() => {
        if (!currentInputDef) return undefined;
        return {
            type: 'youonKakucho' as const,
            char: currentInputDef.char,
            inputDef: currentInputDef
        };
    }, [currentInputDef]);

    return {
        headingChars,
        handleInput,
        getHighlightClassName,
        reset,
        isInvalidInputTarget,
        currentTarget,
        errorHighlightLayer: currentErrorHighlightLayer,
        clearErrorState,
        targetLayerIndices: useMemo(() => {
            const layers = new Set([2, 3]);
            if (currentErrorHighlightLayer !== null) {
                layers.add(currentErrorHighlightLayer);
            }
            return Array.from(layers);
        }, [currentErrorHighlightLayer]),
        stage,
        isSpecialRowHighlightActive,
    };
};

export default useYouonKakuchoPractice;
