import { useMemo, useState, useEffect, useCallback, useRef } from 'react';
import { functionKeyMaps } from '../data/keymapData'; // functionKeyMaps は不要かもしれないが、念のため残す
import {
    PracticeHookProps,
    PracticeInputInfo,
    PracticeInputResult,
    PracticeHookResult,
    CharInfoAlphaSymbol2, // 型を AlphaSymbol2 に変更
    allAlphaSymbol2CharInfos, // データ配列を AlphaSymbol2 に変更
    PracticeHighlightResult,
    PracticeStatus,
} from './usePracticeCommons';

// Define stages for the practice mode (copied from useAlphaSymbolPractice1)
type AlphaSymbolPractice1Stage = 'initialSymbolKey' | 'targetSymbolChar';

const useAlphaSymbolPractice2 = ({ // フック名を変更
    isActive,
    layers,
}: PracticeHookProps): PracticeHookResult => {
    const [status, setStatus] = useState<PracticeStatus>('idle');
    const [stage, setStage] = useState<AlphaSymbolPractice1Stage>('initialSymbolKey');
    const prevIsActiveRef = useRef(isActive);
    const [sequentialTargetIndex, setSequentialTargetIndex] = useState<number>(0); // useAlphaSymbolPractice1 のシーケンシャルロジックを流用

    // The actual symbol to be typed (e.g., '’', '"', ':', ';')
    const actualSymbolTarget = useMemo(() => {
        if (sequentialTargetIndex < 0 || sequentialTargetIndex >= allAlphaSymbol2CharInfos.length) {
            return null;
        }
        return allAlphaSymbol2CharInfos[sequentialTargetIndex]?.char ?? null;
    }, [sequentialTargetIndex]);

    // ヘッダー文字 (固定)
    const headingChars = useMemo(() => {
        if (!isActive) return [];
        return ['\'', '"', ':', ';']; // ヘッダー文字 ’ を ' に変更
    }, [isActive]);

    // Advance to the next symbol in the sequence
    const advanceToNextSymbol = useCallback(() => {
        setSequentialTargetIndex(prevIndex => {
            const nextIndex = (prevIndex + 1) % allAlphaSymbol2CharInfos.length;
            return nextIndex;
        });
        setStage('initialSymbolKey'); // Always expect "記号" key first for the new symbol
        setStatus('waiting');
    }, [setSequentialTargetIndex, setStage, setStatus]);

    // リセット関数
    const reset = useCallback(() => {
        setStage('initialSymbolKey');
        setStatus('idle');
        setSequentialTargetIndex(0); // Reset to the first symbol
    }, [setStage, setStatus, setSequentialTargetIndex]);

    // 初期化とアクティブ状態の監視
    useEffect(() => {
        if (!isActive && prevIsActiveRef.current) {
            reset();
        }
        if (isActive) {
            // Ensure stage is 'initialSymbolKey' and status is 'waiting' when practice becomes active
            // and also set the first target if it's the very first activation.
            if (!prevIsActiveRef.current) { // Just became active
                setSequentialTargetIndex(0); // Ensure it starts from the first symbol
                setStage('initialSymbolKey');
                setStatus('waiting');
            } else { // Already active, but props might have changed (e.g. gIdx/dIdx, though not used here)
                 // For this specific hook, if it's already active, we usually don't need to do much
                 // unless gIdx/dIdx were used to determine the target, which they are not here.
                 // We ensure status is waiting if it somehow got into another state.
                if (status !== 'running' && status !== 'correct' && status !== 'incorrect') {
                    setStatus('waiting');
                }
            }
        }
        prevIsActiveRef.current = isActive;
    }, [isActive, reset, setStatus, setSequentialTargetIndex, setStage, status]); // Added status

    // 入力処理
    const handleInput = useCallback((inputInfo: PracticeInputInfo): PracticeInputResult => {
        if (!isActive || !actualSymbolTarget) {
            return { isExpected: false, shouldGoToNext: false, errorLayer: null };
        }
        if (inputInfo.type !== 'release') {
            return { isExpected: false, shouldGoToNext: false, errorLayer: null };
        }

        const pressCode = inputInfo.pressCode; // 0-based
        const layer10 = layers[10] ?? []; // レイヤー10を参照
        const pressedKeyOnLayer10 = layer10[pressCode]?.trim();

        let isExpected = false;
        let nextStatus: PracticeStatus = status;
        let errorLayer: number | null = null;

        if (stage === 'initialSymbolKey') {
            if (pressedKeyOnLayer10 === '記号') { // レイヤー10の「記号」キー
                isExpected = true;
                setStage('targetSymbolChar');
                nextStatus = 'waiting';
            } else {
                isExpected = false;
                nextStatus = 'incorrect';
                errorLayer = 10; // エラーレイヤーは10
            }
        } else if (stage === 'targetSymbolChar') {
            if (pressedKeyOnLayer10 && pressedKeyOnLayer10 === actualSymbolTarget) {
                isExpected = true;
                nextStatus = 'correct';
                advanceToNextSymbol(); // 次のシーケンシャルターゲットへ
            } else {
                isExpected = false;
                nextStatus = 'incorrect';
                errorLayer = 10; // エラーレイヤーは10
                setStage('initialSymbolKey'); // 不正解時は「記号」キー入力に戻す
            }
        }

        setStatus(nextStatus);

        return { isExpected, shouldGoToNext: false, errorLayer };
    }, [
        isActive,
        layers,
        actualSymbolTarget,
        stage,
        setStatus,
        advanceToNextSymbol,
        status,
    ]);

    // ハイライト処理
    const getHighlightClassName = useCallback((keyName: string, layoutIndex: number): PracticeHighlightResult => {
        const noHighlight: PracticeHighlightResult = { className: null, overrideKey: null };
        if (!isActive) {
            return noHighlight;
        }

        if (layoutIndex === 10) { // レイヤー10を対象
            const trimmedKeyName = keyName.trim();
            if (stage === 'initialSymbolKey' && trimmedKeyName === '記号') {
                return { className: 'bg-blue-100', overrideKey: null };
            }
            if (stage === 'targetSymbolChar' && actualSymbolTarget && trimmedKeyName === actualSymbolTarget) {
                return { className: 'bg-blue-100', overrideKey: null };
            }
        }
        return noHighlight;
    }, [isActive, stage, actualSymbolTarget]);

    const isInvalidInputTarget = useCallback((pressCode: number, layoutIndex: number, keyIndex: number): boolean => {
        if (!isActive || status !== 'incorrect') return false;
        return layoutIndex === 10 && keyIndex === pressCode; // レイヤー10で判定
    }, [isActive, status]);

    const clearErrorState = useCallback(() => {
        setStatus('waiting');
    }, [setStatus]);

    return {
        headingChars,
        handleInput,
        getHighlightClassName,
        reset,
        status,
        targetChar: actualSymbolTarget ?? undefined, // ヘッダーハイライト用には常に実際のターゲット記号を渡す
        errorHighlightLayer: status === 'incorrect' ? 10 : null,
        isInvalidInputTarget,
        clearErrorState,
        targetLayerIndex: 10,
        targetLayerIndices: [10],
    };
};

export default useAlphaSymbolPractice2;